<template>
  <div>
    <video autoplay muted loop :src="backgroundVideo" class="background-video"></video>
    <SearchBar @search="fetchWeather" />
    <LocationList
      :locations="savedLocations"
      @remove="removeLocation"
      @view="fetchWeather"
    />
    <div v-if="weather" class="weather-info">
      <h2>{{ cityName }}, {{ state }}, {{ country }}</h2>
      <WeatherCard :weather="weather" />
    </div>
  </div>
</template>



<script>
import SearchBar from '../components/SearchBar.vue';
import WeatherCard from '../components/WeatherCard.vue';
import LocationList from '../components/LocationList.vue';
import axios from 'axios';

// Import video files
import clearVideo from '../assets/clear.mp4';
import cloudyVideo from '../assets/cloudy.mp4';
import rainyVideo from '../assets/rainy.mp4';
import snowVideo from '../assets/snow.mp4';
import thunderstormsVideo from '../assets/thunderstorms.mp4';
import defaultVideo from '../assets/default.mp4';

export default {
  components: { SearchBar, WeatherCard, LocationList },
  data() {
    return {
      weather: null, // Store weather data
      savedLocations: [], // Store saved locations
    };
  },
  computed: {
    backgroundVideo() {
      if (!this.weather) return defaultVideo; // Default video
      const condition = this.weather.weather[0].main.toLowerCase();

      if (condition.includes('clear')) return clearVideo;
      if (condition.includes('clouds')) return cloudyVideo;
      if (condition.includes('rain')) return rainyVideo;
      if (condition.includes('snow')) return snowVideo;
      if (condition.includes('thunderstorm')) return thunderstormsVideo;

      return defaultVideo; // Fallback video
    },
  },
  methods: {
    async fetchWeather(city) {
  try {
    const API_KEY = 'ed57883fd55ff80c481683537b82903d'; 

    
    const geoResponse = await axios.get(
      `https://api.openweathermap.org/geo/1.0/direct`,
      {
        params: {
          q: city,
          limit: 1, // Limit to the first result
          appid: API_KEY,
        },
      }
    );

    if (geoResponse.data.length === 0) {
      console.error('City not found');
      return;
    }

    const location = geoResponse.data[0];
    this.cityName = location.name;
    this.state = location.state || 'N/A'; // Some locations may not have a state
    this.country = location.country;

    // Step 2: Use the Weather API to fetch weather details
    const weatherResponse = await axios.get(
      `https://api.openweathermap.org/data/2.5/weather`,
      {
        params: {
          q: city,
          appid: API_KEY,
          units: 'imperial', // Use Fahrenheit
        },
      }
    );

    this.weather = weatherResponse.data;
  } catch (error) {
    console.error('Error fetching weather:', error);
  }
},
  removeLocation(index) {
      this.savedLocations.splice(index, 1); // Remove location from saved locations
    },
  },  
};
</script>

<style scoped>
/* Layout container */
.home-layout {
  display: flex; /* Use flexbox for layout */
  height: 100vh; /* Full viewport height */
  overflow: hidden;
}

/* Sidebar styles */
.sidebar {
  width: 20%; /* Adjust the sidebar width as needed */
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent black */
  color: white;
  padding: 20px;
  overflow-y: auto;
  border-right: 2px solid rgba(255, 255, 255, 0.5); /* Optional border for separation */
} 
.sidebar h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
  text-align: center;
  color: #f1c40f; /* Bright yellow for heading text */
}

/* Main content styles */
.main-content {
  flex: 1; /* Take up the remaining space */
  position: relative;
  padding: 20px;
  color: white;
}

/* Style for video background */
.background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the video covers the entire background */
  z-index: -1; /* Places the video behind other content */
}

/* Scrollable saved locations list */
.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar li {
  margin: 10px 0;
  font-size: 18px;
}

.sidebar button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.sidebar button:hover {
  background-color: #c0392b;
}
.weather-info h2 {
  color: white;
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

</style>
